


.MapBox{
    width: 100vw;
    height: 100vh;
    > div:first-child{
        width: 100%;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: space-around;
        > p{
            text-align: center;
            width: 80%;
        }
    }
    > div:last-child{
        width: 100%;
        height: calc(100vh - 50px);
        background-color: white;
        // border-radius: 50% !important;
    }
}

@mixin borD($n) {
    > div:first-child{
        > div:last-child{
            > div:nth-child(4){
                @if $n == 1{
                    > *{
                        border-radius: 50% !important;
                        overflow: hidden !important;
                        > div{
                            width: 100%;
                            height: 80px;
                            display: flex;
                            flex-direction: column;
                            text-align: center;
                            justify-content: space-evenly;
                            padding-top: 3px;
                            padding-bottom: 10px;
                        }
                    }
                }@else{
                    > *{
                        overflow: hidden !important;
                        > div{
                            width: 100%;
                            display: flex;
                            text-align: center;
                            justify-content: space-between;
                        }
                    }
                }
            }
        }
    }
    
}

.bordeBox{
    @include borD(1)
}

.unBordeBox{
    @include borD(0)
}

.itemInfo{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    > p:last-child{
        font-weight: 400;
        font-size: 14px;
    }
}

.homesItem{
    display: flex;
    align-items: center;
    padding: 20px 10px;
    box-sizing: border-box;
    > div:first-child{
        width: 140px;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        > img{
            width: 100%;
        }
    }
    > div:last-child{
        flex: 1;
        margin-left: 10px;
        margin-right: 10px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        display: flex;
        flex-direction: column;
        > p{
            font-size: 16px;
            font-weight: bold;
        }
        > p:last-child{
            color: #fa5741;
            margin-right: 10px;
        }
        > span{
            font-size: 14px;
            font-weight: 200;
        }
        > span:first-child{
            margin-bottom: 4px;
        }
        > div{
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            > div{
                margin-bottom: 4px;
                margin-right: 4px;
                padding: 2px 4px;
                font-size: 14px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 2px;
            }
            > div:nth-of-type(odd){
                background-color: #e1f5ed;
                color: #a2c4d0;
            }
            > div:nth-of-type(even){
                color: #9fcd9e;
                background-color: #e1f5ed;
            }
        }
    }
}